import React from "react";
import "./styles.css";

function EmojiCard(props) {
  return (
    <div className="image">
      <img
        className="emoji hoverable z-depth-4"
        alt={props.name}
        src={props.image}
        onClick={() => props.shuffle()}
      />
    </div>
  );
}

export default EmojiCard;
